import React,{useState,useEffect} from 'react';
import axios from 'axios';
import './App.css';
import Crypto from './Crypto';
import {Jumbotron} from 'react-bootstrap'


// https://api.coingecko.com/api/v3/coins/markets?vs_currency=inr&order=market_cap_desc&per_page=100&page=1&sparkline=false

function App() {
  const[coins,setCoins]=useState([])
  const[search,setSearch]=useState('')

  useEffect(()=>{
    axios.get("https://api.coingecko.com/api/v3/coins/markets?vs_currency=inr&order=market_cap_desc&per_page=100&page=1&sparkline=false")
    .then(res=>{
      setCoins(res.data);
      // console.log(res.data);
    })
    .catch(error=>{
      console.log(error);
    });
  },[]);

  const handleChange=e=>{
    setSearch(e.target.value);
  };

  const filteredCoins=coins.filter(coin=>
    coin.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="coin-app">
      <Jumbotron className="container">
        <div className="coin-search">
            <h1 className="coin-text">CryptoTable</h1>
            <p><i>App by-Arulanandha Guru</i></p>
            <form >
              <input type="text" placeholder="search" className="coin-input" onChange={handleChange}></input>
            </form>
        </div>
       
      </Jumbotron>
      
      
      
      {filteredCoins.map(coin=>{
        return(
          <Crypto key={coin.id} 
          coinname={coin.name} image={coin.image} symbol={coin.symbol} marketcap={coin.market_cap} price={coin.current_price} priceChange={coin.price_change_percentage_24h} volume={coin.total_volume}/>
        )
      })}
      
    </div>
  );
}

export default App;
