import React from 'react'
import './Crypto.css'
import 'bootstrap/dist/css/bootstrap.min.css';

const Crypto = ({coinname,image,symbol,price,volume,priceChange,marketcap}) => {
    return (
        <div>
            <div className='coin-container '>
                <div className="coin-row">
                    <span className="coin ">
                        <span><img src={image} alt="crypto"></img></span>
                        <span><b>{coinname}</b></span>
                        <span className="coin-symbol">({symbol})</span>
                    </span>
                    <div className="coin-data ">
                        <span className="coin-price" style={{"margin":5}}><b>PRICE:</b>Rs.{price.toLocaleString()}  </span>
                        <span className="coin-volume" style={{"margin":5}}><b>Volume:</b>Rs.{volume.toLocaleString()} </span><br></br>
                       {priceChange<0?(<span className="coin-percent red" style={{"margin":5}}>{priceChange.toFixed(2)}%</span>):(<span className="coin-percent green" style={{"margin":5}}>{priceChange.toFixed(2)}%</span>)} <br></br>
                       <span className="coin-marketcap" style={{"margin":5}}><i>MarketCap:</i> {marketcap.toLocaleString()}</span>
                    </div>
                </div> 
               
            </div>
        </div>
    )
}

export default Crypto
